.support{
    padding: 50px 30px;
    background-color: #FFF;
    color: #333;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.support>h3{
    text-align: center;
    padding: 30px;
    margin: 0px;
}
.support>p{
    text-align: center;
    padding: 0px 30px 30px 30px;
    margin: 0px;
}
.support>.row>.col-6>img{
    width: 100%;
    height: auto;
}
.support>.row>.col-6>p{
    font-size: 20px;
    text-align: center;
    padding: 20px;
    margin: 0px;
}
.support>.row>.col-6>.highlighted-text{
    font-size: 60px;
    font-weight: bold;
    color: #A0CE4E;
}
.support>.row>.col-6>p>a{
    background-color: #A0CE4E;
    color: #FFF;
    border: 1px solid #FFF;
    padding: 20px;
    text-transform: uppercase;
    text-decoration: none;
}