.purple-diamond-container{
    background-color: transparent;
    height: 0px;
}
.purple-diamond{
    border: 1px solid #A46498;
    transform: rotate(45deg);
    /* z-index: 2; */
    width: 40px;
    height: 40px;
    background-color: #A46498;
    left: 50%;
    position: relative;
    top: -20px;
}