/* The flip box container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */

.flip-box {
    background-color: transparent;
    width: 300px;
    height: 200px;
    /* border: 1px solid #f1f1f1; */
    -webkit-perspective: 1000px;
            perspective: 1000px;
    border-radius: 5px;
    /* Remove this if you don't want the 3D effect */
}

/* This container is needed to position the front and back side */

.flip-box-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    -webkit-transition: -webkit-transform 0.8s;
    transition: -webkit-transform 0.8s;
    transition: transform 0.8s;
    transition: transform 0.8s, -webkit-transform 0.8s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */

.flip-box:hover .flip-box-inner {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

/* Position the front and back side */

.flip-box-front, .flip-box-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

/* Style the front side */

.flip-box-front {
    background-color: #BBB;
    color: #333;
    padding: 10px;
}

/* Style the back side */

.flip-box-back {
    background-color: #A0CE4E;
    color: #FFF;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    padding: 10px;
}

.extension>.col-4>.flip-box>.flip-box-inner>.flip-box-front>svg {
    width: 30px;
    height: 30px;
}

.extension>.col-4>.flip-box>.flip-box-inner>.flip-box-back>h4 {
    font-size: 16px;
}

.extension>.col-4>.flip-box>.flip-box-inner>.flip-box-back>p {
    font-weight: bold;
}

.extension>.col-4>.flip-box>.flip-box-inner>.flip-box-back>a {
    background: #8EB645;
    padding: 10px;
    color: #FFF;
    text-decoration: none;
}
