.fusion-sb-toggle-wrapper {
    -webkit-order: 2;
            order: 2;
    width: 36px;
    height: 36px;
    display: block;
    position: relative;
    z-index: 2;
    float: right;
}

.fusion-sb-toggle {
    display: block;
    position: relative;
    -webkit-transition: none;
    transition: none;
    pointer-events: auto;
    border-top: 36px solid #333;
    border-left: 36px solid transparent;
}

/* Sliding Bar status */

.expaned {
    height: 30vh;
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
}

.not-expaned {
    padding-top: 0;
    padding-bottom: 0;
    height: 0;
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
}

/* Button status */

.toggled::after {
    left: -15px;
    top: -35px;
    color: #FFF;
    font-size: 20px;
    font-weight: bold;
    display: block;
    position: absolute;
    content: '-';
    -webkit-font-smoothing: antialiased;
    line-height: normal;
    text-shadow: none;
    -webkit-transition: none;
    transition: none;
    font-weight: bold
}

.not-toggled::after {
    left: -15px;
    top: -35px;
    color: #FFF;
    font-size: 18px;
    font-weight: bold;
    display: block;
    position: absolute;
    content: '+';
    -webkit-font-smoothing: antialiased;
    line-height: normal;
    text-shadow: none;
    -webkit-transition: none;
    transition: none;
    font-weight: bold
}
