.green-diamond-container{
    background-color: transparent;
    height: 0px;
}
.green-diamond{
    border: 1px solid #A0CE4E;
    transform: rotate(45deg);
    /* z-index: 2; */
    width: 40px;
    height: 40px;
    background-color: #A0CE4E;
    left: 50%;
    position: relative;
    top: -20px;
}