.option-list {
    background: #333;
    padding: 75px 30px;
}
.option-list>.list-img>.row>.col-md-4{
    padding: 20px;
}
.option-list>.list-img>.row>.col-md-4>.item {
    background-color: rgba(255, 255, 255, 0.9);
    padding: 30px;
    min-height: 467px;
    height: auto;
    text-align: center;
}

.option-list>.list-img>.row>.col-md-4>.item>.img-container {
    border: 10px solid rgba(000, 000, 000, .04);
    margin-bottom: 24px;
}

.option-list>.list-img>.row>.col-md-4>.item>.img-container>img {
    width: 100%;
    height: auto;
}

.option-list>.list-img>.row>.col-md-4>.item>h2 {
    font-family: museoslab300, Arial, Helvetica, sans-serif;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0px;
    font-style: normal;
    font-size: 18px;
    color: #333333;
    margin: 0;
}

.option-list>.list-img>.row>.col-md-4>.item>p {
    text-align: center;
    font-size: 13px;
    line-height: 26px;
    margin-top: 0;
    margin-bottom: 20px;
    color: #747474;
    font-style: normal;
    font-family: PTSan;
    font-weight: 400;
    letter-spacing: 0px;
}

.option-list>.list-img>.row>.col-md-4>.item>a {
    border: 1px solid #333333;
    border-radius: 2px;
    padding: 9px 20px;
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 0px;
    font-family: PTSan;
    font-style: normal;
    font-size: 12px;
    line-height: 26px;
    text-decoration: none;
    color: #333;
}