.blue-diamond-container{
    background-color: transparent;
    height: 0px;
}
.blue-diamond{
    border: 1px solid #0091CD;
    transform: rotate(45deg);
    /* z-index: 2; */
    width: 40px;
    height: 40px;
    background-color: #0091CD;
    left: 50%;
    position: relative;
    top: -20px;
}