.slidingbar-area {
    color: #898989;
    widows: 100%;
    position: absolute;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 13px;
}

.slidingbar-area .widget-title {
    margin-bottom: 20px;
    text-transform: uppercase;
    color: #DDD;
    font-size: 13px;
}

.fusion-sliding-bar {
    width: 100%;
    text-align: left;
    top: 0;
    left: 0;
    right: 0;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin: 0;
    transition: all .4s ease;
    background-color: #333;
    z-index: 2;
    overflow: hidden;
    position: relative;
    padding: 30px;
}

.recent-works-items>a>img:hover {
    opacity: .5;
}

.fusion-sb-toggle-wrapper {
    order: 2;
    width: 36px;
    height: 36px;
    display: block;
    position: relative;
    z-index: 2;
    float: right;
}

.fusion-sb-toggle {
    display: block;
    position: relative;
    transition: none;
    pointer-events: auto;
    border-top: 36px solid #333;
    border-left: 36px solid transparent;
}

/* Sliding Bar status */

.expaned {
    height: 30vh;
    transition: all .4s ease;
}

.not-expaned {
    padding-top: 0;
    padding-bottom: 0;
    height: 0;
    transition: all .4s ease;
}

/* Button status */

.toggled::after {
    left: -15px;
    top: -35px;
    color: #FFF;
    font-size: 20px;
    font-weight: bold;
    display: block;
    position: absolute;
    content: '-';
    -webkit-font-smoothing: antialiased;
    line-height: normal;
    text-shadow: none;
    transition: none;
    font-weight: bold
}

.not-toggled::after {
    left: -15px;
    top: -35px;
    color: #FFF;
    font-size: 18px;
    font-weight: bold;
    display: block;
    position: absolute;
    content: '+';
    -webkit-font-smoothing: antialiased;
    line-height: normal;
    text-shadow: none;
    transition: none;
    font-weight: bold
}