.thumbnail {
    background-color: #212121;
    padding: 55px 20px 55px;
}

.item {
    padding: 25px;
}

.item>.img {
    width: 100%;
    height: 250px;
    overflow: hidden;
}

.item>.img>img:hover{
    transform: scale(1.2);
    transition-duration: 1s;
}
.item>.img>img {
    width: 100%;
    height: auto;
}

