div {
    display: block;
}

a {
    text-decoration: none;
}

.side-bar-container {
    position: fixed;
    top: 0;
    right: 0px;
    z-index: 3;
}

.side-bar-toggle {
    color: #A0CE4E;
    width: 52px;
    height: 56px;
    cursor: pointer;
    background-color: #FFF;
    float: left;
    margin-top: 150px;
    margin-left: -52px;
    position: relative;
    text-align: center;
    line-height: 56px;
    box-shadow: -1px 1px 5px black;
    border-radius: 3px 0px 0px 3px;
    border-right: 0;
    z-index: 4;
}

.side-bar-toggle>a{
    font-size: 30px;
    color: #333;
}

.side-bar-content {
    background-color: #FFF;
    position: relative;
    height: 100vh;
    /* width: 40ch; */
    overflow: scroll;
    box-shadow: -1px 1px 5px black;
}

.side-bar-container .buy-button {
    color: #FFF;
    width: 100%;
    height: 45px;
    line-height: 45px;
    font-size: 15px;
    border: none;
    margin-top: 20px;
}
.fusion-button{
    display: inline-block;
    position: relative;
    zoom: 1;
}
.button-default{
    background: #a0ce4e;
}
.side-bar-container .ss-button {
    font-weight: normal;
    letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    transition: all 0.3s;
    border-radius: 2px;
}

.side-bar-container .ss-policies {
    font-size: 13px;
    text-align: center;
}

.side-bar-container .ss-sep {
    padding: 0 10px;
    color: #777777;
}

.side-bar-container .ss-policies a {
    color: rgba(0, 0, 0, 0.75);
}

.side-bar-container .ss-content {
    border-bottom: 1px solid #dddddd;
    padding-bottom: 30px;
    margin: 0 30px 30px;
}

.side-bar-container .ss-title {
    font-size: 15px;
    color: #000000;
    text-transform: uppercase;
    text-align: center;
    display: block;
    margin-bottom: 15px;
}

.side-bar-container .wide-button {
    margin-right: 2%;
}

.side-bar-container .ss-layout-button {
    display: inline-block;
    width: 48%;
    height: 30px;
    line-height: 30px;
    font-size: 13px;
    color: #000000;
    border: 1px solid #dddddd;
}

.side-bar-container .ss-desc {
    font-size: 13px;
    color: #000000;
    line-height: 20px;
    margin-bottom: 35px;
    display: inline-block;
    text-align: center;
}

.side-bar-container .images {
    text-align: center;
}

.clearfix{
    clear: both;
}

#content:after, .clearfix:after {
    content: "";
    display: table;
    clear: both;
}
.thumb{
    padding: 2px;
}
.thumb>img{
    width: 100%;
    height: auto;
}
.expanded{
    /* margin-left: -40ch; */
    width: 40vh;
    transition: all 1s ease;
}
.not-expanded{
    /* margin-left: 0ch; */
    width: 0vh;
    transition: all 1s ease;
}