.footer .summary {
    background-color: #333;
    color: #777;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    padding: 30px;
}

.footer .summary .summary-column {
    padding: 30px;
}

.footer .summary .summary-column a {
    color: #999;
    text-decoration: none;
}

.footer .summary .summary-column ul li {
    list-style: none;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #999;
}

.footer .summary .summary-column ul li a:hover {
    color: #FFF;
}

.footer .copy-right {
    background-color: #222;
    color: #666;
    border-top: 1px solid #666;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    text-align: center;
    padding: 20px;
}

.footer .copy-right .notice{
    font-size: 13px;
}

.footer .copy-right .notice a {
    color: #999;
    padding: 5px;
}

.footer .copy-right .social a {
    color: #999;
    padding: 5px;
}